.slide{
    position: absolute;
    top:0;
    left: 0;
    min-width: 100%;
    height: 100%;
    background-color: #fff;
    transform: translateX(100%);
    opacity: 0;
    // transition: 0.5s ease;
}
.active-slide{
    opacity: 1;
    transform: translate(0);
    transition: 0.8s ease-out;
}
.inactive-slide{
    opacity: 0;
    transform:translateX(-100%);
    transition: 0.8s ease-out;
}
.next-slide{
    opacity: 0;
    transform:translateX(100%);
    transition: 0.8s ease-out;
}

