#login-btn-submit,#forgot-pass-btn-submit{
    font-size:100%;
    position: relative;
    outline:none;
    border:none;
    border-radius: 140px;
    padding: 10px 50px;
    color:#fff;
    overflow: hidden;
    background-image: linear-gradient(45deg, #e5213c, #a31f22);
    cursor:pointer;
    box-shadow: 0px 0px 0px #00000055;
    transition: 0.3s ease;
}
#login-btn-submit::after,#forgot-pass-btn-submit::after{
    content:"";
    position: absolute;
    top:-50%;
    left: 0;
    width: 20%;
    height: 200%;
    opacity: 0.7;
    background:#fff;
    transform:rotate(45deg)
    translate(-50px,50px);
    filter: blur(10px);
    transition: 0.5s ease;
}
#login-btn-submit:hover::after,#forgot-pass-btn-submit:hover::after{
    transform:rotate(45deg)
    translate(150px,-150px);
}
#login-btn-submit:hover,#forgot-pass-btn-submit:hover{
    box-shadow: 0px 3px 3px 0px #00000055;

}




